import Table from "../../ui/Table";
import useAppStore from "components/context/app/Hooks";
import { DashboardDiagnostics, Feature } from "types";
import NotLicensed from "components/pages/notLicensed";
import { useParams } from "react-router-dom";
import { useQuery } from "react-query3";
import { CustomColumns, DashboardSession } from "../../../types";
import { formatDate } from "components/utils/utils";
import {List, Space, Typography} from "antd"

const DashboardSessions = () => {
  const { id } = useParams();
  const { licensed } = useAppStore();
  const { data: sessions } = useQuery<any>(`/dashboard/${id}/diagnostics`, {
    enabled: !!id && licensed(Feature.Dashboard),
    select: (data: DashboardDiagnostics) => {
      return data?.sessions;
    },
  });

  const columns: CustomColumns<DashboardSession>[] = [
    {
      title: "Id",
      key: "id",
      dataIndex: "id",
    },
    {
      title: "User Name",
      key: "userName",
      dataIndex: "userName",
    },
    {
      title: "Last Active",
      key: "lastTouched",
      dataIndex: "lastTouched",
      render: (text) => {
        return formatDate(text);
      },
    },
    {
      title: "Endpoints",
      key: "endpoints",
      dataIndex: "endpoints",
      render: (text, data) => {
        return data ? data.endpoints.length : 0;
      },
    },
  ];

  if (!licensed(Feature.Dashboard)) {
    return (
      <NotLicensed title="Sessions are only available for licensed instances." />
    );
  }

  return (
    <Table
      rowKey={(record: DashboardSession) => record.id}
      data={sessions}
      columns={columns}
      expandable={{
        expandedRowRender: (record: DashboardSession) => {
          return (
            <List
              size="default"
              dataSource={record.endpoints}
              renderItem={(item) => {
                return (
                  <List.Item style={{ padding: 12, paddingLeft: 48, paddingRight: 0 }}>
                    <List.Item.Meta title={renderColumn(item.id, true)} />
                    <Space style={{ padding: 0, margin: 0}}>
                      {renderColumn(`Average Execution Time:`)}
                      {renderColumn(`${item.averageExecutionTime}`)}
                    </Space>
                  </List.Item>
                );
              }}
            />
          );
        },
      }}
    />
  );
};

export default DashboardSessions;

function renderColumn<T>(value: keyof T, copyable?: boolean) {
  return (
    <Typography.Text
      style={{
        fontFamily: "SFProDisplay-Regular",
        fontSize: 14,
      }}
      copyable={copyable}
    >
      {value}
    </Typography.Text>
  );
}